/* Photography.css */
.photography-section {
  /* Êï¥‰ΩìÂÆπÂô®Ê†∑Âºè */
}

.photography-section > p {
    margin-bottom: 25px;
    color: #666;
}

.photo-gallery {
  display: grid;
  /* ÂàõÂª∫ÂìçÂ∫îÂºèÁΩëÊ†ºÔºåÊúÄÂ∞èÂàóÂÆΩ 200pxÔºåÊúÄÂ§ß 1fr */
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr)); 
  gap: 15px; /* ÁΩëÊ†ºÈó¥Ë∑ù */
}

.photo-item {
  position: relative;
  cursor: pointer;
  overflow: hidden; /* ÈöêËóèÊ∫¢Âá∫ÁöÑË¶ÜÁõñÂ±Ç */
  border-radius: 8px; /* ËΩªÂæÆÂúÜËßí */
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.photo-item:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 15px rgba(0,0,0,0.15);
}

.photo-item img {
  display: block; /* Ê∂àÈô§ÂõæÁâáÂ∫ïÈÉ®Á©∫Èöô */
  width: 100%;
  height: 200px; /* Âõ∫ÂÆöÈ´òÂ∫¶ÔºåÊàñ‰ΩøÁî® aspect-ratio */
  object-fit: cover; /* Ë£ÅÂâ™ÂõæÁâá‰ª•Â°´ÂÖÖÂÆπÂô® */
  transition: transform 0.3s ease;
}

.photo-item:hover img {
    transform: scale(1.05); /* ÂõæÁâáËΩªÂæÆÊîæÂ§ß */
}

.photo-overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(to top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0) 100%);
  color: white;
  padding: 20px 10px 10px 10px;
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none; /* ÂÖÅËÆ∏ÁÇπÂáªÁ©øÈÄèÂà∞ÂõæÁâá */
}

.photo-item:hover .photo-overlay {
  opacity: 1;
}

.photo-overlay p {
    margin: 0;
    font-size: 0.9rem;
    font-weight: 500;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

/* Lightbox Styles */
.lightbox-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.85); /* Êõ¥Ê∑±ÁöÑËÉåÊôØ */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  padding: 20px;
  box-sizing: border-box;
  cursor: pointer; /* ÊöóÁ§∫ÁÇπÂáªÁ©∫ÁôΩÂ§ÑÂèØÂÖ≥Èó≠ */
}

.lightbox-content {
  position: relative;
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  max-width: 90vw; /* ÈôêÂà∂ÊúÄÂ§ßÂÆΩÂ∫¶ */
  max-height: 90vh; /* ÈôêÂà∂ÊúÄÂ§ßÈ´òÂ∫¶ */
  display: flex;
  flex-direction: column; /* ÂÜÖÂÆπÂûÇÁõ¥ÊéíÂàó */
  cursor: default; /* ÂÜÖÂÆπÂå∫ÂüüÊÅ¢Â§çÈªòËÆ§ÂÖâÊ†á */
  box-shadow: 0 5px 20px rgba(0,0,0,0.3);
}

.lightbox-content img {
  display: block;
  max-width: 100%;
  max-height: calc(80vh - 100px); /* ÈôêÂà∂ÂõæÁâáÈ´òÂ∫¶ÔºåÁªôÊ†áÈ¢òÁïôÁ©∫Èó¥ */
  object-fit: contain; /* ‰øùÊåÅÂõæÁâáÊØî‰æã */
  margin: 0 auto; /* ÂõæÁâáÊ∞¥Âπ≥Â±Ö‰∏≠ */
}

.lightbox-caption {
  padding: 15px 0 0 0;
  text-align: center;
  color: #333;
}

.lightbox-caption h4 {
  margin: 0 0 5px 0;
  font-size: 1.2rem;
}
.lightbox-caption p {
  margin: 5px 0;
  font-size: 0.95rem;
}
.lightbox-caption small {
    color: #777;
}

.lightbox-close {
  position: absolute;
  top: 10px;
  right: 15px;
  background: none;
  border: none;
  font-size: 2rem;
  color: #888;
  cursor: pointer;
  padding: 0;
  line-height: 1;
  transition: color 0.2s ease;
}

.lightbox-close:hover {
  color: #333;
} 