/* ByAuthor.css */
.quotes-by-author {
  display: flex;
  gap: 30px; /* ‰ΩúËÄÖÂàóË°®ÂíåÂºïÁî®Âå∫ÂüüÁöÑÈó¥Ë∑ù */
}

.author-list-container {
  flex: 1;
  min-width: 180px;
  max-width: 220px;
}

.author-list {
  max-height: 400px; /* Â¶ÇÊûú‰ΩúËÄÖÂæàÂ§öÔºåÂèØ‰ª•ÈôêÂà∂È´òÂ∫¶Âπ∂ÊªöÂä® */
  overflow-y: auto;
  padding-right: 10px; /* ‰∏∫ÊªöÂä®Êù°ÁïôÁ©∫Èó¥ */
}

.author-quotes-section {
  flex: 3;
}

.sort-options {
  margin-bottom: 15px;
  font-size: 0.9rem;
  color: #555;
}

.sort-options button {
  background: none;
  border: 1px solid #ccc;
  padding: 4px 8px;
  margin-left: 8px;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.2s ease, border-color 0.2s ease;
}

.sort-options button:hover {
  background-color: #f0f0f0;
}

.sort-options button.active {
  background-color: #e0e0e0;
  border-color: #bbb;
  font-weight: bold;
}

.select-prompt {
    flex: 3;
    color: #888;
    font-style: italic;
    text-align: center;
    margin-top: 50px;
}

/* ÂìçÂ∫îÂºèË∞ÉÊï¥ */
@media (max-width: 768px) {
  .quotes-by-author {
    flex-direction: column;
  }
  .author-list-container {
     max-width: 100%;
     margin-bottom: 20px;
  }
   .author-list {
     max-height: 200px; /* ÁßªÂä®Á´ØÂáèÂ∞èÈ´òÂ∫¶ */
   }
   .select-prompt {
       margin-top: 20px;
       flex: unset;
   }
}

.by-author-container {
  display: flex;
  gap: 20px; /* Space between columns */
}

.author-list-column {
  flex: 0 0 25%; /* ‰øÆÊîπ‰∏∫25% */
  max-width: 25%; /* ‰øÆÊîπ‰∏∫25% */
  border-right: 1px solid #eee; /* Optional separator */
  padding-right: 20px; /* Space before the border */
  /* Á°Æ‰øùÂàóË°®Êú¨Ë∫´ÂèØ‰ª•ÊªöÂä®Â¶ÇÊûúÂÜÖÂÆπËøáÂ§ö */
  max-height: calc(100vh - 150px); /* Á§∫‰æãÈ´òÂ∫¶ÔºåÊ†πÊçÆ‰Ω†ÁöÑÈ°µÂ§¥È°µËÑöË∞ÉÊï¥ */
  overflow-y: auto;
}

.author-details-column {
  flex: 1 1 75%; /* ‰øÆÊîπ‰∏∫75% */
  min-width: 0; /* Prevents content overflow issues in flex items */
}

/* --- Styles for Author List (Left Column) --- */
.author-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.author-list-item {
  margin-bottom: 10px;
}

.author-link {
  display: flex;
  align-items: center;
  padding: 8px 12px;
  border-radius: 6px;
  text-decoration: none;
  color: #333;
  transition: background-color 0.2s ease;
}

.author-link:hover,
.author-list-item.active .author-link { /* Style for hovered and active (based on mouse hover state) */
  background-color: #e9ecef;
  color: #0056b3;
}

.author-avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 12px;
  object-fit: cover;
}

.author-name {
  font-weight: 500;
}

/* --- Styles for Author Details (Right Column) - NEW LAYOUT --- */
.author-details-placeholder {
  color: #888;
  text-align: center;
  padding: 40px 0;
}

.author-profile-card {
  border: 1px solid #ddd; /* Á§∫‰æãËæπÊ°Ü */
  border-radius: 12px;
  overflow: hidden; /* Á°Æ‰øùÂúÜËßíÂØπÂÜÖÈÉ®ÂÖÉÁ¥†ÁîüÊïà */
  background-color: #fff;
}

.profile-background-image {
  height: 200px; /* ËÉåÊôØÂõæÈ´òÂ∫¶Ôºå‰Ω†ÂèØ‰ª•Ë∞ÉÊï¥ */
  background-size: cover;
  background-position: center;
  position: relative; /* Áî®‰∫éÂèØËÉΩÁöÑÂè†Âä†ÂÜÖÂÆπÔºåÂ¶ÇÊûúÈúÄË¶ÅÁöÑËØù */
}

.profile-header-section {
