.dino-game-container {
  width: 100%;
  height: 100px; /* Adjust height as needed */
  position: relative;
  overflow: hidden;
  border-bottom: 1px solid #ccc; /* Visual separator */
  margin-bottom: 15px; /* Space below the animation */
}

.dino {
  width: 40px; /* Adjust size */
  height: 40px; /* Adjust size */
  background-color: #666; /* Simple gray dino */
  position: absolute;
  bottom: 0;
  left: 20px; /* Starting position */
  /* Ë∑≥Ë∑ÉÂä®ÁîªÂ∞ÜÈÄöËøáÊ∑ªÂä† .jump Á±ªÊù•Ëß¶Âèë */
}

.dino.jump {
  animation: dino-jump 0.7s ease-out;
}

@keyframes dino-jump {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-60px); /* Ë∑≥Ë∑ÉÈ´òÂ∫¶ */
  }
  100% {
    transform: translateY(0);
  }
}

.obstacles {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50px; /* Max height of obstacles */
  /* pointer-events: none; // Â¶ÇÊûúÈöúÁ¢çÁâ©‰∏çÈúÄË¶Å‰∫§‰∫í */
}

.cactus {
  width: 20px; /* Adjust size */
  height: 40px; /* ÈªòËÆ§‰ªô‰∫∫ÊéåÈ´òÂ∫¶ÔºåÂèØ‰ª•Ê†πÊçÆÁ±ªÂûãË∞ÉÊï¥ */
  background-color: green; /* Simple green cactus */
  position: absolute;
  bottom: 0;
  /* Âä®ÁîªÂ∞Ü‰ªéÂè≥‰æßÂºÄÂßãÔºåÂêëÂ∑¶ÁßªÂä® */
  right: -20px; /* ÂàùÂßã‰ΩçÁΩÆÂú®ÂÆπÂô®Âè≥‰æßÂ§ñÈÉ® */
  animation-name: cactus-move;
  animation-timing-function: linear;
  /* animation-duration Áî± JS Âä®ÊÄÅËÆæÁΩÆ */
}

/* Â¶ÇÊûúÈúÄË¶Å‰∏çÂêåÈ´òÂ∫¶ÁöÑ‰ªô‰∫∫ÊéåÔºåÂèØ‰ª•Ê∑ªÂä†Êõ¥Â§öÁ±ªÔºå‰æãÂ¶ÇÔºö
.cactus.short { height: 20px; }
.cactus.tall { height: 40px; }
*/

@keyframes cactus-move {
  0% {
    transform: translateX(0); /* Áõ∏ÂØπ‰∫éÂÖ∂ÂàùÂßã right:-20px ÁöÑ‰ΩçÁΩÆ */
  }
  100% {
    /* ÁßªÂä®Êï¥‰∏™ÂÆπÂô®ÂÆΩÂ∫¶ÂÜçÂä†‰∏ä‰ªô‰∫∫ÊéåËá™Ë∫´ÂÆΩÂ∫¶ÔºåÁ°Æ‰øùÂÆåÂÖ®ÁßªÂá∫Â±èÂπïÂ∑¶‰æß */
    /* Ëøô‰∏™ÂÄºÈúÄË¶ÅË∂≥Â§üÂ§ßÔºåÁ°Æ‰øùËÉΩÁßªÂá∫„ÄÇ‰ΩøÁî®‰∏Ä‰∏™ÂæàÂ§ßÁöÑË¥üÂÄº„ÄÇ*/
    /* ÊàñËÄÖÊõ¥Á≤æÁ°ÆÂú∞ËÆ°ÁÆó: calc(-100vw - 20px) ‰ΩÜËøôÂèñÂÜ≥‰∫éÂÆπÂô®ÊòØËßÜÂè£ÂÆΩÂ∫¶ */
    /* ËøôÈáåÊàë‰ª¨‰ΩøÁî®‰∏Ä‰∏™Ë∂≥Â§üÂ§ßÁöÑÂõ∫ÂÆöÂÄºÔºåÁ°Æ‰øùËÉΩÁßªÂá∫Â§ßÂ§öÊï∞ÂÆπÂô® */
    transform: translateX(-300px); /* Ëøô‰∏™ÂÄºÂèØ‰ª•Ê†πÊçÆ‰Ω†ÁöÑÂÆπÂô®ÊúÄÂ§ßÂÆΩÂ∫¶Ë∞ÉÊï¥ */
  }
}

.ground {
  width: 100%;
  height: 2px;
  background-color: #333;
  position: absolute;
  bottom: -1px; /* Ensure it's flush with bottom border or slightly overlaps */
  left: 0;
} 