/* SelfIntroCard.css */
.self-intro-card {
  background-color: rgba(255, 255, 255, 0.8); /* ÂçäÈÄèÊòéËÉåÊôØÂ¢ûÂä†Â±ÇÊ¨°ÊÑü */
  padding: 30px 40px;
  border-radius: 12px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
  margin-top: 40px; /* ‰∏éÊ¨¢ËøéËØ≠‰øùÊåÅË∑ùÁ¶ª */
  text-align: center;
  max-width: 500px; /* ÈôêÂà∂Âç°ÁâáÂÆΩÂ∫¶ */
  width: 90%; /* Âú®Â∞èÂ±èÂπï‰∏äÈÄÇÂ∫îÂÆΩÂ∫¶ */
  backdrop-filter: blur(5px); /* ÊØõÁéªÁíÉÊïàÊûú (ÈúÄË¶ÅÊµèËßàÂô®ÊîØÊåÅ) */
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.title-container {
    position: relative; /* Áî®‰∫éÂÆö‰ΩçÁÆ≠Â§¥ÊåáÁ§∫ */
    display: inline-block; /* ËÆ©ÂÆπÂô®ÂåÖË£πÂÜÖÂÆπ */
    margin-bottom: 20px;
    padding-left: 50px; /* ‰∏∫Â∑¶‰æßÁöÑÁÆ≠Â§¥ÁïôÂá∫Á©∫Èó¥ */
}

.card-title-link {
    text-decoration: none;
    color: #2980b9; /* Ê†áÈ¢òÈìæÊé•È¢úËâ≤ */
    transition: color 0.3s ease;
}

.card-title-link:hover {
    color: #1abc9c; /* Èº†Ê†áÊÇ¨ÂÅúÊó∂ÂèòËâ≤ */
    text-decoration: underline; /* ÊÇ¨ÂÅú‰∏ãÂàíÁ∫ø */
}

.card-title {
  font-size: 1.8rem;
  font-weight: 600;
  margin: 0; /* ÈáçÁΩÆÈªòËÆ§ËæπË∑ù */
  display: inline; /* ËÆ©Ê†áÈ¢òÂíåÁÆ≠Â§¥Âú®Âêå‰∏ÄË°å */
}

/* ÁÇπÂáªÊåáÁ§∫ÁÆ≠Â§¥Ê†∑Âºè */
.click-indicator {
    font-size: 1.5rem;
    color: #e67e22; /* ÈÜíÁõÆÁöÑÈ¢úËâ≤ */
    margin-left: 10px;
    cursor: pointer; /* ÊöóÁ§∫ÂèØÁÇπÂáª */
    display: inline-block;
    transition: transform 0.3s ease;
    animation: bounce 1.5s infinite; /* Ê∑ªÂä†Ë∑≥Âä®Âä®Áîª */
}

.card-description {
  font-size: 1rem;
  color: #555;
  line-height: 1.6;
}

/* ÁÆ≠Â§¥Ë∑≥Âä®Âä®Áîª */
@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-8px);
  }
  60% {
    transform: translateY(-4px);
  }
}

/* Â∞èÂ±èÂπïÈÄÇÂ∫î */
@media (max-width: 600px) {
  .self-intro-card {
    padding: 20px;
  }
  .card-title {
    font-size: 1.5rem;
  }
  .welcome-message {
     font-size: 2rem;
  }
  .start-button {
    padding: 12px 30px;
    font-size: 1.2rem;
  }
  .title-container {
      padding-left: 40px; /* Ë∞ÉÊï¥ÁßªÂä®Á´ØÁÆ≠Â§¥Á©∫Èó¥ */
  }
} 