.post-meta {
  font-size: 0.9rem;
  color: #868e96; /* ÁÅ∞Ëâ≤ÂÖÉ‰ø°ÊÅØ */
  margin-bottom: 20px;
}

.post-content {
  line-height: 1.7; /* Â¢ûÂä†Ë°åÈ´òÊèêÈ´òÂèØËØªÊÄß */
  color: #495057; /* Ê≠£ÊñáÈ¢úËâ≤ */
}

.post-content p {
  margin-bottom: 1em; /* ÊÆµËêΩÈó¥Ë∑ù */
}

.post-content h2 {
  font-size: 1.4rem;
  margin-top: 1.8em;
  margin-bottom: 0.8em;
  padding-bottom: 0.3em;
  border-bottom: 1px solid #dee2e6;
  color: #495057;
}

.post-content h3 {
  font-size: 1.2rem;
  margin-top: 1.5em;
  margin-bottom: 0.6em;
  color: #495057;
}

.post-content strong,
.post-content b {
  font-weight: 600;
}

.post-content em,
.post-content i {
  font-style: italic;
}

.post-content a {
  color: #3498db;
  text-decoration: none;
}

.post-content a:hover {
  text-decoration: underline;
}

.post-content blockquote {
  border-left: 3px solid #ced4da;
  margin: 1.5em 0;
  padding: 0.5em 1em;
  color: #6c757d;
  background-color: #f8f9fa;
}

.post-content code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
  background-color: #f1f3f5;
  padding: 0.2em 0.4em;
  border-radius: 3px;
  font-size: 0.9em;
}

.post-content pre {
  background-color: #f1f3f5;
  padding: 1em;
  border-radius: 4px;
  overflow-x: auto; /* ‰ª£Á†ÅÂùóÊ®™ÂêëÊªöÂä® */
}

